.accordion__trigger:hover {
  @apply border-b cursor-pointer border-unit-secondary;
}

.accordion__trigger.is-open {
  @apply font-semibold bg-[url('../img/svg/icon-minus.svg')] border-b border-unit-primary;
}

.accordion__trigger h3 {
  @apply text-2xl;
  margin-top: calc(var(--leading) * 1.25rem);
  margin-bottom: 0;
  padding-right: 2em;
  letter-spacing: 0.025em;
  font-family: var(--text-font-stack);
  font-weight: 400;
  line-height: 1.4;
}

.widget-accordion h3 {
  margin-top: 0;
  font-size: var(--base-font-size);
}

@media (min-width: 1720px) {

  .widget-accordion h3 {
    @apply text-2xl;
  }
}

/* summary element */
summary {
  margin-top: calc(var(--leading) * 1rem);
