/*
* This file contains all default application-wide Sass and CSS3 variables.
*/

:root {

    --base-line-height: var(--leading);

    --ubc-blue: 0, 33, 69; /* #002145 */
    --ubc-blue-sea: 0, 58, 121; /* #003a79 */
    --ubc-blue-cobalt: 0, 85, 183; /* #0055b7 */
    --ubc-blue-neptune: 64, 180, 229; /* #40b4e5 */
    --ubc-blue-sky: 97, 193, 233; /* #61c1e9 */
    --ubc-blue-cornflower: 151, 212, 233; /* #97d4e9 */
    --ubc-blue-polar: 222, 241, 248; /* #def1f8 */
    --ubc-blue-frost: 246, 251, 253; /* #f6fbfd */

    --ubc-grey-1: 47, 93, 124; /* #2f5d7c */
    --ubc-grey-2: 94, 134, 159; /* #5e869f */
    --ubc-grey-3: 152, 178, 195; /* #98b2c3 */
    --ubc-grey-4: 195, 208, 219; /* #c3d0db */
    --ubc-grey-5: 213, 222, 228; /* #d5dee4 */
    --ubc-grey-6: 234, 238, 241; /* #eaeef1 */

    --black: 0, 0, 0; /* #000 */
    --white: 255, 255, 255; /* #fff */
    --grey-50: 250, 250, 250; /* #fafafa */
    --grey-100: 245, 245, 245; /* #f5f5f5 */
    --grey-200: 238, 238, 238; /* #eeeeee */
    --grey-300: 224, 224, 224; /* #e0e0e0 */
    --grey-400: 189, 189, 189; /* #bdbdbd */
    --grey-500: 158, 158, 158; /* #9e9e9e */
    --grey-600: 117, 117, 117; /* #757575 */
    --grey-700: 97, 97, 97; /* #616161 */
    --grey-800: 66, 66, 66; /* #424242 */
    --grey-900: 33, 33, 33; /* #212121 */

    --error: 255, 124, 124; /* #ff7c7c */
    --warning: 248, 230, 5; /* #f8e605 */
    --success: 87, 237, 174; /* #57edae */
    --status: rgba(var(--ubc-blue-neptune), 1);
    --text-color: rgba(var(--grey-900), 1);

    --default-text-color: var(--text-color);
    --default-color-link: rgba(var(--ubc-blue-cobalt), 1);
    --default-color-link-hover: rgba(var(--ubc-blue), 1);
    --default-color-link-active: rgba(var(--ubc-blue), 1);

    --title-weight: 600;

    /* Forms */
    --input-border-radius: 0;
    --input-text: rgba(var(--grey-700), 1);
    --input-border: rgba(var(--grey-300), 1);
    --input-bg: rgba(var(--white), 1);
    --input-disabled-color: rgba(var(--grey-100), 1);
    --input-disabled-bg: rgba(var(--grey-700), 1);
    --input-disabled-border: rgba(var(--grey-700), 1);
    --input-select-border: rgba(var(--grey-300), 1);
    --input-select-bg: rgba(var(--white), 1);
    --input-select-text: rgba(var(--grey-700), 1);
    --input-check-radio-color: rgba(var(--ubc-blue), 1);
    --input-check-radio-border: rgba(var(--ubc-blue), 1);
    --input-placeholder-color: rgba(var(--grey-300), 1);
    --input-required-color: rgba(var(--error), 1);
    --focus-bg: rgba(var(--ubc-blue-neptune), 0.5);
    --focus-inner-bg: rgba(var(--ubc-blue), 1);
    --focus-required-bg: rgba(255, 124, 124, 0.5);
    --focus-required-inner-bg: rgb(214, 47, 47);
    /* Buttons */
    --button-text: rgba(var(--white), 1);
    --button-bg: rgba(var(--ubc-blue), 1);
    --button-border: transparent;
    --button-text--hover: rgba(var(--white), 1);
    --button-bg--hover: rgba(var(--ubc-blue-sea), 1);
    --button-border--hover: transparent;
    --button-light-bg: rgba(var(--ubc-blue-polar), 1);
    --button-light-border: transparent;
    --button-light-text: rgba(var(--black), 1);
    --button-light-bg--hover: rgba(var(--ubc-blue-frost), 1);
    --button-light-border--hover: transparent;
    --button-light-text--hover: rgba(var(--black), 1);
    --button-dark-bg: rgba(var(--ubc-blue), 1);
    --button-dark-border: transparent;
    --button-dark-text: rgba(var(--white), 1);
    --button-dark-bg--hover: rgba(var(--ubc-blue-cobalt), 1);
    --button-dark-border--hover: transparent;
    --button-dark-text--hover: rgba(var(--white), 1);
    --button-transparent-bg: rgba(var(--white), 0.2);
    --button-transparent-border: transparent;
    --button-transparent-text: rgba(var(--white), 1);
    --button-transparent-bg--hover: rgba(var(--white), 0.4);
    --button-transparent-border--hover: transparent;
    --button-transparent-text--hover: rgba(var(--white), 1);

    --color-primary: #0055b7 !important;
    --color-secondary: #6ec4e8 !important;
    --color-tertiary: #bee6f4 !important;
    --color-accent: #00acce !important;
    --color-alt-primary: #4d3900 !important;
    --color-alt-secondary: #d4a411 !important;
    --color-alt-tertiary: #edc95e !important;
    --color-alt-accent: #f2e5c0 !important;
    --color-link: #0055b7;
    --color-link-hover: #002145;
    --color-link-active: #002145;
    --leading: 1.5;
    --base-font-size: 1rem;
    --text-font-stack: Inter, Arial, sans-serif;
    --alternate-font-stack: merriweather, georgia, cambria, "Times New Roman", times, serif;
    --code-font-stack:  menlo, monaco, consolas, "Liberation Mono", "Courier New", monospace;
}
